[x-cloak] {
  display: none !important;
}

@layer components {
  .container {
    @apply max-w-[1312px];
  }

  .menu-item-chevron-down {
    @apply after:content-[''] after:base-transition after:ml-0.5 after:mt-0.5 after:top-0 lg:after:relative after:absolute after:right-0 lg:after:mb-0 after:block after:w-5 after:h-5 after:bg-cover after:bg-[url(../images/sprite.svg#chevron-down)];
  }

  .menu-item-chevron-down-rotate {
    @apply after:lg:rotate-0 after:rotate-90;
  }

  .menu-item-dropdown {
    @apply relative flex flex-col w-full items-start menu-item-chevron-down cursor-pointer lg:flex-row lg:justify-center;
  }

  .submenu-item {
    @apply base-transition block cursor-pointer hover:text-quicksilver;
  }

  .submenu {
    @apply relative block bg-white rounded-lg top-0 pr-0 pl-4 py-4 space-y-4 lg:top-10 lg:left-0 lg:absolute lg:pr-20;
  }

  .menu-link {
    @apply lg:normal-case after:mt-2 lg:after:mt-0 text-chineseblack-500 uppercase after:transition-width after:duration-300 after:ease-in-out after:content-[''] group-hover:lg:after:w-full hover:lg:after:w-full after:block after:h-px after:w-0 after:bg-chineseblack-400;
  }

  .base-transition {
    @apply transition-all duration-300 ease-in-out;
  }

  .menu-link-hover {
    @apply lg:after:w-full;
  }

  .footer-menu-link {
    @apply hover:text-quicksilver;
  }
}
